import React from "react";
import { graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import Layout from "@components/layout";
import Seo from "@components/seo";
import BreadCrumb from "@components/common/breadcrumb";
import TonWalletScreen from "@screens/ton-wallet";
import { useLocation } from "@reach/router";

const TonWalletPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Ton wallet Development Company", link: null },
  ];

  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <main>
        <TonWalletScreen db={db} />
      </main>
    </Layout>
  );
};

export const Head = (props) => {
  const { data } = props;
  const fileUrl =
    data?.tonWalletDevImg?.childImageSharp?.gatsbyImageData?.images
      ?.fallback?.src;
  const location = useLocation();
  return (
    <Seo
      title="TON-Wallet-Development-Company | Justtry Technologies"
      metaTitle="TON-Wallet-Development-Company | TON-Wallet-Development-Company Services"
      description="Leverage our phenomenal TON wallet development services and revolutionize the crypto world with a feature-rich, user-friendly, ultra-secure digital wallet."
      image={fileUrl}
      siteFirestore={data?.site?.siteMetadata?.firestore}
      slug={location.pathname.split("/")?.[1]}
    ></Seo>
  );
};

export default TonWalletPage;

export const queryData = graphql`
  query MyQuery {
    tonWalletDevImg: file(
      relativePath: { eq: "ton-wallet-development.png" }
    ) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
