import React, { useState } from "react";
import { Container, Row, Col, Accordion } from "react-bootstrap";
import * as styles from "./css/faq.module.css";

const Faq = () => {
  const [visibleItems, setVisibleItems] = useState(4);
  const [hasMore, setHasMore] = useState(true);

  const handleLoadMore = () => {
    setVisibleItems(visibleItems + 4);
    if (visibleItems + 4 >= faqData?.length) {
      setHasMore(false);
    }
  };

  const faqData = [
    {
      question: "1. Is building a TON wallet worth it?",
      answer:
        " Absolutely! The TON wallet with its ultra-fast transactionsand user-friendly interface has garnered quite an attraction which, in turn, has skyrocketed the demand.",
    },
    {
      question: "2. How secure are your TON wallets?",
      answer:
        " Our TON wallets employ robust encryption and security protocols to safeguard your user’s valuable assets.",
    },
    {
      question: "3. I am a beginner. Will your TON wallet be easy to use?",
      answer:
        "No crypto experience? No problem! Our TON wallet has an intuitive user interface designed for seamless navigation, ideal for beginners",
    },
    {
      question: "4. What features can I expect in your TON wallet?",
      answer:
        "You can expect features such as multi-currency support, swift fund transfers, secure storage, and effortless integration with DApps.",
    },
    {
      question: "5. Why should I choose your TON wallet?",
      answer:
        "Not all TON wallets are built equal. Justtry’s TON wallets offer robust security, beginner-friendly interfaces, and advanced features that you have been looking for!",
    },
    {
      question:
        "6. These FAQs haven't answered my question. How can I get in touch?",
      answer:
        "Feel free to reach out to our dedicated support team through our contact page with any inquiries or concerns regarding our TON wallet development services.",
    },
  ];

  return (
    <div className={styles.faqsection}>
      <Container>
        <div className={styles.faq_row}>
          <Row className="faqcontainer justify-content-space-between align-items-center">
            <Col lg={10} className="m-auto">
              <div className="faqtitle text-center">
                <h2>Resolve your unanswered queries effortlessly</h2>
              </div>
            </Col>
          </Row>
          <Row>
            <Col lg={12} md={12}>
              <Accordion className={"faq_accordion"}>
                {faqData?.length > 0 &&
                  faqData?.slice(0, visibleItems)?.map((ele, ind) => {
                    return (
                      <Accordion.Item eventKey={ind}>
                        <Accordion.Header>{ele?.question}</Accordion.Header>
                        <Accordion.Body>{ele?.answer}</Accordion.Body>
                      </Accordion.Item>
                    );
                  })}
                {faqData?.length > 4 && hasMore && (
                  <div
                    aria-label="ton-fag load btn"
                    role="button"
                    tabIndex="0"
                    onKeyDown={(e) => { }}
                    onClick={handleLoadMore}
                    className="decenter_hero_sec_btn d-flex justify-content-center"
                  >
                    <button className="demo_btn">Load More</button>
                  </div>
                )}
              </Accordion>
            </Col>
          </Row>
        </div>
      </Container>
    </div>
  );
};

export default Faq;
